  position: absolute;
  height: 400px;
  width: 21%;
  top: 0;
  right: 0;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #242526;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

