* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #fff;
}
nav {
  border-bottom: 1px solid var(--cardborder);
  display: flex;
  height: 50px;
  width: 100%;
  align-items: center;
  justify-content: space-around;
  text-transform: capitalize;
  background: transparent !important;
  z-index: 999999;
  padding: 30px;
  color: var(--navtext);
}
nav a,
.logo {
  color: var(--text-color);
  font-weight: 400;
  font-family: "Montserrat";
  color: var(--navtext);
}

.logo .for_mobile {
  display: none;
}
.mainHeadingNav {
  font-size: 18px;
}
.mainHeadingNav p span {
  font-weight: 700;
}
nav ul {
  display: flex;
  justify-content: space-around;
  width: 40%;
}
ul li {
  cursor: pointer;
  color: var(--navtext);
}
nav li a .icons {
  color: #fff;
  display: none;
  font-size: 20px;
}
li a span {
  transition: 0.2s ease;
  color: var(--navtext);
}
li a span:hover {
  color: var(--button);
}

nav ul li p {
  color: #fff;
  font-size: 10px;
  margin-top: 5px;
  display: none;
  letter-spacing: 0.5px;
  margin-right: 3px;
}

/* ----------- design for Dropdown  search ------------ */
.dropdown_data {
  z-index: 100;
  position: absolute;
  top: 45px;
  right: 100px;
  display: flex;
  flex-direction: column;
  width: 250px;
  background-color: #343a40;
  font-family: "poppins", sans-serif;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
  transition: 0.2s ease-in-out;
  cursor: pointer;
  padding: 5px;
  border-radius: 5px;
}

/* ---------after for the triangle------------- */

.li {
  font-size: 12px;
  padding: 15px 10px;
  border-radius: 5px;
  display: block;
}
.drp_down_holder {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.dropdown_data .li:hover {
  background-color: var(--feelhover);
}

.user__login {
  display: flex;
}
.user__login .li {
  margin: 0 5px;
}
.logedin_data {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  border-radius: 20px;
}
/*//notif*/
.notif_btn {
  margin-left: 20px;
}
.notif_btn i {
  font-size: 18px;
  color: var(--headingcolor);
  /*background: var(--button);*/
  padding: 8px;
  border-radius: 50%;
}
.notif_len {
  font-size: 60px;
